import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchCards } from '../../store/slices/cardsSlice';
import { RootState } from '../../store/store';
import api from '../../services/api';

interface ImportError {
  row: number;
  data: any;
  error: string;
}

interface ImportResult {
  message: string;
  total: number;
  success: number;
  failed: number;
  errors: ImportError[];
}

const BatchImportCards: React.FC = () => {
  const dispatch = useDispatch();
  const { user } = useSelector((state: RootState) => state.auth);
  const [file, setFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<ImportResult | null>(null);
  const [error, setError] = useState<string>('');
  const [isOverwriteMode, setIsOverwriteMode] = useState(false); // 
  const [cardOptions, setCardOptions] = useState<{
    types: string[];
    factions: string[];
    categories: { [key: string]: string[] };
    typeDetails: Array<{ id: string; name: string }>;
    factionDetails: Array<{ id: string; name: string }>;
    categoryDetails: { [key: string]: Array<{ id: string; name: string; description?: string }> };
  }>({ 
    types: [], 
    factions: [], 
    categories: {},
    typeDetails: [],
    factionDetails: [],
    categoryDetails: {}
  });

  // 
  useEffect(() => {
    const fetchCardOptions = async () => {
      try {
        const response = await api.get('/options/card-options');
        setCardOptions(response.data);
      } catch (error) {
        console.error(':', error);
        // 
        setCardOptions({
          types: ['story', 'character', 'hero'],
          factions: ['neutral', 'hero1', 'hero2', 'hero3'],
          categories: {
            story: ['event', 'background'],
            character: ['character'],
            hero: ['hero']
          },
          typeDetails: [
            { id: 'story', name: '' },
            { id: 'character', name: '' },
            { id: 'hero', name: '' }
          ],
          factionDetails: [
            { id: 'neutral', name: '' },
            { id: 'hero1', name: '1' },
            { id: 'hero2', name: '2' },
            { id: 'hero3', name: '3' }
          ],
          categoryDetails: {
            story: [
              { id: 'event', name: '', description: '' },
              { id: 'background', name: '', description: '' }
            ],
            character: [
              { id: 'character', name: '', description: '' }
            ],
            hero: [
              { id: 'hero', name: '', description: '' }
            ]
          }
        });
      }
    };

    if (user) {
      fetchCardOptions();
    }
  }, [user]);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile) {
      if (selectedFile.type !== 'text/csv' && !selectedFile.name.endsWith('.csv')) {
        setError('CSV');
        return;
      }
      setFile(selectedFile);
      setError('');
      setResult(null);
    }
  };

  const handleDownloadTemplate = async () => {
    try {
      const response = await api.get('/cards/import-template', {
        responseType: 'blob',
      });
      
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'card_import_template.csv');
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
    } catch (error) {
      setError('');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!file) {
      setError('CSV');
      return;
    }

    if (!user) {
      setError('');
      return;
    }

    setLoading(true);
    setError('');
    setResult(null);

    try {
      const formData = new FormData();
      formData.append('csvFile', file);
      formData.append('overwriteMode', isOverwriteMode.toString()); // 

      const response = await api.post('/cards/batch-import', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      setResult(response.data);
      
      // 
      dispatch(fetchCards({}) as any);
      
    } catch (error: any) {
      setError(error.response?.data?.message || '');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h2 className="text-2xl font-bold mb-6"></h2>
      
      <div className="bg-white rounded-lg shadow-md p-6 mb-6">
        <div className="mb-4">
          <h3 className="text-lg font-semibold mb-2"></h3>
          <ul className="list-disc list-inside text-sm text-gray-600 space-y-1">
            <li>CSV</li>
            <li>(name), (type), (category), (cost), (effect)</li>
            <li>type{cardOptions.typeDetails.length > 0 ? cardOptions.typeDetails.map(t => `${t.id}(${t.name})`).join(', ') : '...'}</li>
            <li>faction{cardOptions.factionDetails.length > 0 ? cardOptions.factionDetails.map(f => `${f.id}(${f.name})`).join(', ') : '...'}</li>
            <li>categorytype
              {cardOptions.typeDetails.length > 0 ? (
                <ul className="ml-4 mt-1">
                  {cardOptions.typeDetails.map(type => {
                    const categories = cardOptions.categoryDetails[type.id] || [];
                    return (
                      <li key={type.id}>
                        <strong>{type.name}</strong>: {categories.length > 0 ? categories.map(c => `${c.id}(${c.name})`).join(', ') : ''}
                      </li>
                    );
                  })}
                </ul>
              ) : '...'}
            </li>
            <li>attackhealth0</li>
            <li>isPublictrue/falsefalse</li>
          </ul>
        </div>

        <button
          onClick={handleDownloadTemplate}
          className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded mb-4"
        >
          CSV
        </button>
      </div>

      <form onSubmit={handleSubmit} className="bg-white rounded-lg shadow-md p-6">
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            CSV
          </label>
          <input
            type="file"
            accept=".csv"
            onChange={handleFileChange}
            className="block w-full text-sm text-gray-500
              file:mr-4 file:py-2 file:px-4
              file:rounded-md file:border-0
              file:text-sm file:font-semibold
              file:bg-blue-50 file:text-blue-700
              hover:file:bg-blue-100"
          />
        </div>

        <div className="mb-4">
          <label className="flex items-center space-x-3">
            <input
              type="checkbox"
              checked={isOverwriteMode}
              onChange={(e) => setIsOverwriteMode(e.target.checked)}
              className="h-4 w-4 text-red-600 focus:ring-red-500 border-gray-300 rounded"
            />
            <span className="text-sm font-medium text-gray-700">
              
            </span>
          </label>
          <p className="text-xs text-gray-500 mt-1 ml-7">
            {isOverwriteMode 
              ? " " 
              : ""}
          </p>
        </div>

        {error && (
          <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
            {error}
          </div>
        )}

        <button
          type="submit"
          disabled={!file || loading}
          className={`w-full py-2 px-4 rounded-md text-white font-medium
            ${!file || loading
              ? 'bg-gray-400 cursor-not-allowed'
              : 'bg-blue-500 hover:bg-blue-600'
            }`}
        >
          {loading ? '...' : ''}
        </button>
      </form>

      {result && (
        <div className="mt-6 bg-white rounded-lg shadow-md p-6">
          <h3 className="text-lg font-semibold mb-4"></h3>
          
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
            <div className="text-center p-3 bg-blue-50 rounded">
              <div className="text-2xl font-bold text-blue-600">{result.total}</div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <div className="text-center p-3 bg-green-50 rounded">
              <div className="text-2xl font-bold text-green-600">{result.success}</div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <div className="text-center p-3 bg-red-50 rounded">
              <div className="text-2xl font-bold text-red-600">{result.failed}</div>
              <div className="text-sm text-gray-600"></div>
            </div>
            <div className="text-center p-3 bg-gray-50 rounded">
              <div className="text-2xl font-bold text-gray-600">
                {result.total > 0 ? Math.round((result.success / result.total) * 100) : 0}%
              </div>
              <div className="text-sm text-gray-600"></div>
            </div>
          </div>

          {result.errors.length > 0 && (
            <div>
              <h4 className="font-medium text-red-600 mb-3"></h4>
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {result.errors.map((error, index) => (
                      <tr key={index}>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          {error.row}
                        </td>
                        <td className="px-6 py-4 text-sm text-gray-900">
                          <pre className="text-xs bg-gray-100 p-2 rounded overflow-x-auto">
                            {JSON.stringify(error.data, null, 2)}
                          </pre>
                        </td>
                        <td className="px-6 py-4 text-sm text-red-600">
                          {error.error}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default BatchImportCards;
