import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Card } from '../../types';
import { RootState } from '../../store/store';
import SearchableSelect from '../common/SearchableSelect';
import api from '../../services/api';

interface CardFormProps {
  card?: Card;
  onSubmit: (cardData: Partial<Card>) => void;
  onCancel: () => void;
}

const CardForm: React.FC<CardFormProps> = ({ card, onSubmit, onCancel }) => {
  const { user } = useSelector((state: RootState) => state.auth);
  const [formData, setFormData] = useState({
    name: '',
    type: '' as '' | '' | '' | '',
    category: '',
    cost: '',
    attack: 0,
    health: 0,
    effect: '',
    flavor: '',
    image: '',
    faction: 'neutral', // neutral
    isPublic: false
  });

  const [factions, setFactions] = useState<{ id: string; name: string; icon: string }[]>([]);
  const [types, setTypes] = useState<{ id: string; name: string; icon: string }[]>([]);
  const [categories, setCategories] = useState<any>({});

  useEffect(() => {
    const fetchConfig = async () => {
      try {
        const response = await api.config.getConfig();
        setFactions(response.data.factions || []);
        setTypes(response.data.types || []);
        setCategories(response.data.categories || {});

        // 
        if (card) {
          setFormData(prev => ({
            ...prev,
            faction: card.faction,
            type: card.type,
            category: card.category
          }));
        }
      } catch (error) {
        console.error(':', error);
      }
    };
    fetchConfig();
  }, [card]); // card

  useEffect(() => {
    if (card) {
      setFormData(prev => ({
        ...prev,
        name: card.name,
        type: card.type,
        category: card.category,
        cost: card.cost,
        attack: card.attack || 0,
        health: card.health || 0,
        effect: card.effect,
        flavor: card.flavor || '',
        image: card.image || '',
        faction: card.faction,
        isPublic: card.isPublic
      }));
    }
  }, [card]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value, type } = e.target;
    
    if (type === 'checkbox') {
      const checkbox = e.target as HTMLInputElement;
      setFormData(prev => ({
        ...prev,
        [name]: checkbox.checked
      }));
    } else if (name === 'attack' || name === 'health') {
      setFormData(prev => ({
        ...prev,
        [name]: parseInt(value) || 0
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }));
    }

    // 
    if (name === 'type') {
      setFormData(prev => ({
        ...prev,
        category: getDefaultCategory(value as any),
        // 
        attack: value === '' ? prev.attack : 0,
        health: value === '' ? prev.health : 0
      }));
    }
  };

  const getDefaultCategory = (type: 'story' | 'character' | 'hero') => {
    const typeCategories = categories[type];
    if (typeCategories && typeCategories.length > 0) {
      return typeCategories[0].name;
    }
    return '';
  };

  const getCategoryOptions = () => {
    const typeCategories = categories[formData.type] || [];
    return typeCategories.map((category: any) => (
      <option key={category.id} value={category.name}>
        {category.name} - {category.description}
      </option>
    ));
  };

  // 
  useEffect(() => {
    const fetchLatestConfig = async () => {
      try {
        const response = await api.config.getConfig();
        setCategories(response.data.categories || {});
        
        // 
        const typeCategories = response.data.categories?.[formData.type] || [];
        if (typeCategories.length > 0 && !typeCategories.find((cat: any) => cat.name === formData.category)) {
          setFormData(prev => ({
            ...prev,
            category: typeCategories[0].name
          }));
        }
      } catch (error) {
        console.error(':', error);
      }
    };
    
    fetchLatestConfig();
  }, [formData.type]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // 
    if (!formData.name.trim() || !formData.category.trim() || !formData.cost.trim() || !formData.effect.trim()) {
      alert('');
      return;
    }

    // 
    if (formData.type === '') {
      if (formData.attack < 0 || formData.health <= 0) {
        alert('00');
        return;
      }
    }

    onSubmit(formData as any);
  };

  const getCardPreview = () => {
    return (
      <div className="bg-gradient-to-br from-gray-800 to-gray-900 rounded-xl p-4 border-2 border-yellow-500">
        <div className="flex justify-between items-start mb-3">
          <h3 className="text-lg font-bold text-white">{formData.name || ''}</h3>
          <span className="text-yellow-400 font-bold text-xl bg-yellow-600 bg-opacity-50 px-2 py-1 rounded-full">
            {formData.cost || '?'}
          </span>
        </div>

        {/*  */}
        {formData.image && (
          <div className="mb-3">
            <img
              src={formData.image}
              alt=""
              className="w-full h-32 object-cover rounded border border-gray-500"
              onError={(e) => {
                const target = e.target as HTMLImageElement;
                target.style.display = 'none';
              }}
            />
          </div>
        )}

        <div className="text-sm text-gray-300 mb-3">
          <p><span className="text-blue-400">:</span> {getCardTypeText(formData.type)} - {formData.category}</p>
          <p><span className="text-blue-400">:</span> {getFactionText(formData.faction)}</p>
          {formData.type === '' && (
            <p><span className="text-blue-400">/:</span> 
              <span className="text-red-400 font-bold ml-1">{formData.attack}</span>/
              <span className="text-green-400 font-bold">{formData.health}</span>
            </p>
          )}
        </div>

        <div className="text-gray-200 text-sm mb-3 bg-green-600 bg-opacity-20 p-2 rounded">
          <p className="font-semibold text-green-400">:</p>
          <p className="break-words whitespace-pre-wrap">{formData.effect || '...'}</p>
        </div>

        {formData.flavor && (
          <div className="text-gray-400 text-xs italic bg-gray-600 bg-opacity-30 p-2 rounded whitespace-pre-wrap">
            "{formData.flavor}"
          </div>
        )}

        <div className="mt-3 text-center">
          <span className={`px-2 py-1 rounded-full text-xs ${
            formData.isPublic ? 'bg-green-600 text-white' : 'bg-yellow-600 text-white'
          }`}>
            {formData.isPublic ? '' : ''}
          </span>
        </div>
      </div>
    );
  };

  const getCardTypeText = (type: string) => {
    switch (type) {
      case 'story': return '';
      case 'character': return '';
      case 'hero': return '';
      default: return type;
    }
  };

  const getFactionText = (faction: string) => {
    switch (faction) {
      case 'neutral': return '';
      case 'hero1': return '1';
      case 'hero2': return '2';
      case 'hero3': return '3';
      default: return faction;
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white bg-opacity-10 backdrop-blur-md rounded-xl p-8 max-w-6xl w-full max-h-screen overflow-y-auto">
        <h2 className="text-2xl font-bold text-white mb-6">
           {card ? '' : ''}
        </h2>
        
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/*  */}
          <div>
            <form onSubmit={handleSubmit} className="space-y-6">
              {/*  */}
              <div className="bg-white bg-opacity-10 rounded-lg p-4">
                <h3 className="text-white font-semibold mb-4"> </h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-2">
                       <span className="text-red-400">*</span>
                    </label>
                    <input
                      type="text"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                      className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=""
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-2">
                       <span className="text-red-400">*</span>
                    </label>
                    <input
                      type="text"
                      name="cost"
                      value={formData.cost}
                      onChange={handleChange}
                      className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=": 2, *, X"
                      required
                    />
                    <p className="text-xs text-gray-400 mt-1">* </p>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-2">
                       <span className="text-red-400">*</span>
                    </label>
                    <select
                      name="type"
                      value={formData.type}
                      onChange={handleChange}
                      className="w-full px-3 py-2 bg-gray-800 border border-gray-500 rounded-md text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                      style={{
                        backgroundColor: '#1a1a1a',
                        color: 'white'
                      }}
                      required
                    >
                      {types.map(type => (
                        <option 
                          key={type.id} 
                          value={type.id} 
                          style={{ backgroundColor: '#2a2a2a', color: 'white' }}
                        >
                          {type.name}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-2">
                       <span className="text-red-400">*</span>
                    </label>
                    <select
                      name="category"
                      value={formData.category}
                      onChange={handleChange}
                      className="w-full px-3 py-2 bg-gray-800 border border-gray-500 rounded-md text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                      style={{
                        backgroundColor: '#1a1a1a',
                        color: 'white'
                      }}
                      required
                    >
                      <option value="" style={{ backgroundColor: '#2a2a2a', color: 'white' }}></option>
                      {getCategoryOptions().map((option: React.ReactElement) => 
                        React.cloneElement(option, {
                          style: { backgroundColor: '#2a2a2a', color: 'white' }
                        })
                      )}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-2"></label>
                    <SearchableSelect
                      options={factions.map(faction => ({ 
                        value: faction.id, 
                        label: faction.name 
                      }))}
                      value={formData.faction}
                      onChange={(value) => setFormData(prev => ({ ...prev, faction: value }))}
                      placeholder="..."
                      className="w-full"
                    />
                  </div>

                  {formData.type === '' && (
                    <>
                      <div>
                        <label className="block text-sm font-medium text-gray-300 mb-2">
                           <span className="text-red-400">*</span>
                        </label>
                        <input
                          type="number"
                          name="attack"
                          value={formData.attack}
                          onChange={handleChange}
                          min="0"
                          className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                        />
                        <p className="text-xs text-gray-400 mt-1">0</p>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-300 mb-2">
                           <span className="text-red-400">*</span>
                        </label>
                        <input
                          type="number"
                          name="health"
                          value={formData.health}
                          onChange={handleChange}
                          min="1"
                          className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                        />
                        <p className="text-xs text-gray-400 mt-1">0</p>
                      </div>
                    </>
                  )}
                </div>
              </div>

              {/*  */}
              <div className="bg-white bg-opacity-10 rounded-lg p-4">
                <h3 className="text-white font-semibold mb-4"> </h3>
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                     <span className="text-red-400">*</span>
                  </label>
                  <textarea
                    name="effect"
                    value={formData.effect}
                    onChange={handleChange}
                    rows={4}
                    className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="..."
                    required
                  />
                  <p className="text-xs text-gray-400 mt-1">
                    {formData.type === '' ? '' :
                     formData.type === '' ? '' :
                     ''}
                  </p>
                </div>

                <div className="mt-4">
                  <label className="block text-sm font-medium text-gray-300 mb-2"></label>
                  <textarea
                    name="flavor"
                    value={formData.flavor}
                    onChange={handleChange}
                    rows={2}
                    className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder=""
                  />
                </div>

                <div className="mt-4">
                  <label className="block text-sm font-medium text-gray-300 mb-2">URL</label>
                  <input
                    type="url"
                    name="image"
                    value={formData.image}
                    onChange={handleChange}
                    className="w-full px-3 py-2 bg-white bg-opacity-10 border border-gray-500 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="http://example.com/card-image.jpg"
                  />
                  {formData.image && (
                    <div className="mt-2">
                      <img
                        src={formData.image}
                        alt=""
                        className="max-w-full h-32 object-cover rounded border border-gray-500"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.style.display = 'none';
                          target.nextElementSibling?.classList.remove('hidden');
                        }}
                      />
                      <div className="hidden text-red-400 text-sm">URL</div>
                    </div>
                  )}
                </div>
              </div>

              {/*  */}
              <div className="bg-white bg-opacity-10 rounded-lg p-4">
                <h3 className="text-white font-semibold mb-4"> </h3>
                <label className="flex items-center space-x-3 cursor-pointer">
                  <input
                    type="checkbox"
                    name="isPublic"
                    checked={formData.isPublic}
                    onChange={handleChange}
                    className="w-4 h-4"
                  />
                  <span className="text-gray-300">
                    
                  </span>
                </label>
                <p className="text-xs text-gray-400 mt-2">
                  
                </p>
              </div>

              <div className="flex space-x-4">
                <button
                  type="button"
                  onClick={onCancel}
                  className="flex-1 bg-gray-600 hover:bg-gray-700 text-white py-3 px-4 rounded transition-colors"
                >
                  
                </button>
                <button
                  type="submit"
                  className="flex-1 bg-green-600 hover:bg-green-700 text-white py-3 px-4 rounded transition-colors"
                >
                  {card ? '' : ''}
                </button>
              </div>
            </form>
          </div>

          {/*  */}
          <div>
            <h3 className="text-white font-semibold mb-4"> </h3>
            {getCardPreview()}
            
            {/*  */}
            <div className="mt-6 bg-white bg-opacity-10 rounded-lg p-4">
              <h4 className="text-white font-semibold mb-3"> </h4>
              <div className="text-sm text-gray-300 space-y-2">
                {formData.type === '' && (
                  <>
                    <p><strong className="text-blue-400"></strong> - </p>
                    <p> <strong>:</strong> </p>
                    <p> <strong>:</strong> </p>
                  </>
                )}
                  {formData.type === '' && (
                  <>
                    <p><strong className="text-green-400"></strong> - </p>
                    <p> </p>
                    <p> </p>
                    <p> 0</p>
                  </>
                )}
                {formData.type === '' && (
                  <>
                    <p><strong className="text-purple-400"></strong> - </p>
                    <p> </p>
                    <p> </p>
                    <p> 1</p>
                  </>
                )}
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  );
};

export default CardForm;
